import * as core from "@actions/core";
import { v4 as uuidv4 } from "uuid";
import { Agent } from "./agent";
import { Downloader, } from "./downloader";
import { Provisioner } from "./provisioner";
async function run() {
    try {
        const [owner, repo] = core
            .getInput("repository", { required: true })
            .split("/");
        const inputs = {
            arch: core.getInput("arch"),
            archive: core.getInput("archive"),
            installPath: core.getInput("installPath") === ""
                ? uuidv4()
                : core.getInput("installPath"),
            name: core.getInput("name"),
            owner,
            platform: core.getInput("platform"),
            repo,
            tag: core.getInput("tag"),
            token: core.getInput("token", { required: true }),
        };
        const downloader = new Downloader(inputs);
        const provisioner = new Provisioner(inputs);
        const agent = new Agent(downloader, provisioner);
        await agent.run();
        // eslint-disable-next-line @typescript-eslint/no-explicit-any
    }
    catch (err) {
        core.setFailed(err.message);
        core.debug(err.stack);
    }
}
run();
